			<div class="flex flex-col sm:flex-row items-center justify-center gap-4">
				<div class="flex flex-col gap-4 w-full text-center text-white font-primary text-sm">
					<p class="text-h5 text-[#E9EAEB] font-normal text-justify leading-5 tracking-wide"> The Assistive Funder Toolkit has been developed by The Bridgespan Group as part of the Pay-What-It-Takes (PWIT) India Initiative—a multiyear, collaborative effort to build a stronger and more resilient social sector in India. It works to address the chronic underfunding of nonprofits’ true costs—including core costs, organizational development, financial resilience, and programmatic needs. Fully funding these expenses would allow an organization to realize the impact for which funders and nonprofits strive.
					</p>
					<p class="text-h5 text-[#E9EAEB] font-normal text-justify leading-5 tracking-wide"> This toolkit is hosted in partnership with the Indian School of Development Management (ISDM) under the aegis of the Centre for Philanthropy for Inclusive Development (CPID), a Centre of Excellence at ISDM dedicated to catalyzing philanthropy as a force for inclusive development through research, learning programs, and convenings with diverse voices.
					</p>
					<p class="text-h5 text-[#E9EAEB] font-normal text-justify leading-5 tracking-wide"> The PWIT principles closely align with CPID’s Six-Point Philanthropy for Inclusive Development Framework, which guides funders on their journey toward more inclusive and impactful giving practices. Together, the PWIT India Initiative and CPID aim to inform and influence the mindsets and practices of funders, equipping them with actionable insights to drive more effective, sustainable, and high-impact funding strategies aimed at strengthening the social sector.
					</p>
